<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区 -->
    <el-card class="box-card">
      <!-- 搜索与添加区域 -->
      <el-row :gutter="20">
        <!-- 搜索 -->
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button>
          </el-input>
        </el-col>
        <!-- 添加用户对话框 -->
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            >添加用户</el-button
          >
        </el-col>
      </el-row>

      <!-- 用户列表 -->
      <el-table :data="userList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label="姓名"></el-table-column>
        <el-table-column prop="email" label="邮箱"></el-table-column>
        <el-table-column prop="mobile" label="手机号"></el-table-column>
        <el-table-column prop="role_name" label="角色"></el-table-column>
        <el-table-column prop="mg_state" label="状态">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="userStateChanged(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <!-- 用户列表:修改，删除，分配 -->
        <el-table-column label="操作" width="260">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content="修改"
              placement="top"
              :open-delay="yanchi"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="clickModify(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              placement="top"
              :open-delay="yanchi"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="userDeleteById(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content="分配"
              placement="top"
              :open-delay="yanchi"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="setRoleDialog(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!-- ------------dialogVisible区域------------------------ -->
    <!-- 添加用户的dialogVisible -->
    <el-dialog
      title="添加用户"
      :visible.sync="addDialogVisible"
      width="50%"
      :close-on-click-modal="false"
      @close="addDialogClosed"
    >
      <!-- 内容主体区 -->
      <span>
        <el-form
          :model="addForm"
          ref="addFormRef"
          :rules="addUsersRules"
          label-width="auto"
        >
          <el-form-item label="用户名" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="addForm.password"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPassword">
            <el-input
              type="password"
              v-model="addForm.checkPassword"
            ></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="addForm.email"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="mobile">
            <el-input v-model.number="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogClosed">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 修改用户的dialogVisible -->
    <el-dialog
      title="修改用户信息"
      :visible.sync="modifyDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="modifyForm"
        :rules="addUsersRules"
        ref="modifyForm"
        label-width="100px"
      >
        <el-form-item label="用户名">
          <el-input v-model="modifyForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="modifyForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="mobile">
          <el-input v-model="modifyForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogClosed">取 消</el-button>
        <el-button type="primary" @click="modifyUserInfo">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 用户分配权限的dialogVisible -->
    <el-dialog
      title="用户分配权限"
      :visible.sync="setRoleDialogVisible"
      width="50%"
    >
      <div>
        <p>当前的用户：{{ userInfo.username }}</p>
        <p>当前的角色：{{ userInfo.role_name }}</p>
        <p>
          分配新角色：
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>
      <span>
        <el-button @click="setRoleDialogColsed">取 消</el-button>
        <el-button type="primary" @click="saveRoleInfo">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Users',
  data() {
    //验证邮箱的规则
    var checkEmail = (rule, value, cb) => {
      //验证邮箱的正则表达式
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        //合法的邮箱
        return cb()
      }
      cb(new Error('请输入合法的邮箱！'))
    }
    //验证手机号规则
    var checkMoble = (rule, value, cb) => {
      const regEmail = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57]|19[0-9]|16[0-9])[0-9]{8}$/
      if (regEmail.test(value)) {
        return cb()
      }
      cb(new Error('请输入合法的手机号！'))
    }
    //校验密码再次输入规则
    var checkPassword = (rule, value, cb) => {
      if (this.addForm.password === value) {
        return cb()
      }
      cb(new Error('两次密码不一致，请重新输入！'))
    }
    return {
      queryInfo: {
        query: '',
        //当前的页数
        pagenum: 1,
        //每页显示的数据条数
        pagesize: 10,
      },
      userList: [],
      total: 0,
      value: true,
      yanchi: parseInt('400'),

      //是否显示增加用户的对话框
      addDialogVisible: false,
      //是否显示修改用户的对话框
      modifyDialogVisible: false,
      //是否显示分配用户的对话框
      setRoleDialogVisible: false,
      //增加表单
      addForm: {
        username: '',
        password: '',
        checkPassword: '',
        email: '',
        mobile: '',
      },
      modifyForm: {},
      addUsersRules: {
        username: [
          { required: true, message: '请输入用户姓名', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur', //触发时机
          },
        ],
        password: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur', //触发时机
          },
        ],
        checkPassword: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          {
            validator: checkPassword,
            tigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          {
            validator: checkEmail,
            tigger: 'blur',
          },
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          {
            validator: checkMoble,
            tigger: 'blur',
          },
        ],
      },
      //分配角色表单
      setRoleForm: { id: '', rid: '' },
      //用户信息
      userInfo: {},
      //所有角色的数据列表
      rolesList: [],
      //选中的id值
      selectedRoleId: [],
    }
  },
  created() {
    this.getUserList()
    console.log('heh')
  },
  methods: {
    //获取用户表单
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo,
      })
      if (res.meta.status !== 200)
        return this.$message.error('获取用户操作失败!')
      this.userList = res.data.users
      this.total = res.data.total
      console.log(res)
    },
    //监听pagesize改变的事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    //监听页码值的改变事件
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    //监听用户状态改变
    async userStateChanged(userInfo) {
      console.log(userInfo)
      //发起这次请求之后，会返回一个propms对象，为了减少propms操作增加await和async
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userInfo.mg_state
        return this.$message.error('更新用户状态失败')
      }
      return this.$message.success('更新用户状态成功')
    },
    //监听增加用户对话框的关闭
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    //按‘确定’提交表单验证
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return
        //可以发起添加用户的网路请求
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          this.$message.error('添加用户失败！')
        }
        this.$message.success('添加用户成功！')
        //隐藏添加用户的对话框
        this.addDialogVisible = false
        //刷新列表
        this.getUserList()
      })
    },
    // 点击修改
    async clickModify(id) {
      console.log(id)
      this.modifyDialogVisible = true
      const { data: res } = await this.$http.get('users/' + id)
      console.log(res)
      if (res.meta.status !== 200)
        return this.$message.error('查询用户信息失败！')
      this.modifyForm = res.data
    },
    //监听修改用户的关闭事件
    editDialogClosed() {
      this.$refs.modifyForm.resetFields()
      this.modifyDialogVisible = false
    },
    //监听添加用户的关闭事件
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
      this.addDialogVisible = false
    },
    //修改用户信息
    modifyUserInfo() {
      //验证表单数据
      this.$refs.modifyForm.validate(async (valid) => {
        if (!valid) return
        //发起修改用户信息的数据请求
        const { data: res } = await this.$http.put(
          'users/' + this.modifyForm.id,
          {
            email: this.modifyForm.email,
            mobile: this.modifyForm.mobile,
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('更新失败，您发生了一些未知错误！')
        }
        //关闭对话框
        this.modifyDialogVisible = false
        this.getUserList()
        return this.$message.success('修改信息成功！')
      })
    },
    //删除用户信息
    async userDeleteById(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      console.log(confirmResult)
      if (confirmResult !== 'confirm') {
        this.getUserList()
        return this.$message.info('已取消删除')
      }
      console.log(id)
      const { data: res } = await this.$http.delete('users/' + id)
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('发生了未知错误，删除失败！')
      }
      this.$message.success('删除成功')
      this.getUserList()
    },
    //分配权限按钮
    async setRoleDialog(user) {
      this.userInfo = user

      //在展示对话框之前，获取所有角色的列表
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200)
        return this.$message.error('获取角色列表失败！')

      this.rolesList = res.data

      this.setRoleDialogVisible = true
    },
    //按‘确定’上传用户信息
    async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('请选择要分配的角色！')
      }

      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectedRoleId,
        }
      )
      console.log(this.selectedRoleId)
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('更新角色失败！')
      }
      this.$message.success('更新成功')
      this.getUserList
      this.setRoleDialogVisible = false
    },
    //监听用户分配角色关闭
    setRoleDialogColsed() {
      this.selectedRoleId = ''
      this.userInfo = {}
    },
  },
}
</script>

<style lang="less" scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}
.el-pagination {
  margin-top: 15px;
}
</style>

