<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome'  }">首页</el-breadcrumb-item>
      <el-breadcrumb-item class="rights">权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <el-table :data="rightList" border stripe>
        <!-- border边框线 stripe隔行变色 -->
        <el-table-column type="index" label="#" width="75"></el-table-column>
        <el-table-column prop="authName" label="权限名称" width="430"></el-table-column>
        <el-table-column prop="path" label="路径" width="430"></el-table-column>
        <el-table-column prop="level" label="权限等级">
          <template id="level" slot-scope="scope">
            <el-tag v-if="scope.row.level=== '0'">一级</el-tag>
            <el-tag v-else-if="scope.row.level=== '1'" type="success">二级</el-tag>
            <el-tag v-else type="warning">三级</el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'Rights',
  data() {
    return {
      rightList: [],
    }
  },
  created() {
    this.getRightList()
  },
  methods: {
    async getRightList() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$message.error('获取权限列表失败！')
      }
      this.rightList = res.data
    },
  },
}
</script>

<style  scoped>
.rights {
  cursor: pointer;
}
</style>