<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="addRoleDialog">添加角色</el-button>
        </el-col>
      </el-row>
      <!--  角色列表-主体区域 -->
      <!-- border边框线 stripe隔行变色 -->
      <el-table :data="roleList" border stripe>
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <!-- <div>{{scope.row}}</div> -->
            <el-row
              v-for="(item1,i1) in scope.row.children"
              :class="['bdbottom',i1 === 0 ? 'bdtop':'','vcenter']"
              :key="item1.id"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <el-row
                  v-for="(item2,i2) in item1.children"
                  :class="[i2 === 0 ? '':'bdtop','vcenter']"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="(item3,i3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(scope.row,item3.id)"
                      type="warning"
                      :class="[i3 === 0 ? '':'bdtop']"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label="角色名称"></el-table-column>
        <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" @click="editRole(scope.row)">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="deleteRole(scope.row)">删除</el-button>
            <el-button
              type="warning"
              icon="el-icon-setting"
              @click="showSetRightDialog(scope.row)"
            >分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分配权限的对话框 -->
    <el-dialog
      title="权限分配"
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightDialogClose"
    >
      <el-tree
        :data="rightsList"
        :props="rightsProps"
        @node-click="setRightsClick"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="setRightTree"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRights">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 添加角色对话框 -->
    <el-dialog title="添加角色" :visible.sync="addRoleDialogVisible" width="50%" @close="addRoleColse">
      <el-form :model="addRoleForm" :rules="addRoleRules" ref="addRoleForm" label-width="100px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span>
        <el-button @click="addRoleDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="subAddRole">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑角色对话框 -->
    <el-dialog title="编辑角色" :visible.sync="editRoleDialogVisible" width="50%">
      <el-form
        :model="rolesRuleForm"
        :rules="addRoleRules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="name">
          <el-input v-model="rolesRuleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="desc">
          <el-input v-model="rolesRuleForm.desc"></el-input>
        </el-form-item>
      </el-form>
      <span>
        <el-button @click="editRoleDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoleOk">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Roles',
  data() {
    return {
      roleList: [],
      rightsList: [],
      // 分配权限的对话框显示与隐藏
      setRightDialogVisible: false,
      // 添加角色的对话框显示与隐藏
      addRoleDialogVisible: false,
      //编辑角色对话框的显示与隐藏
      editRoleDialogVisible: false,

      rightsProps: {
        children: 'children',
        label: 'authName',
      },
      //默认选中的节点数组
      defKeys: [],
      roleId: '',
      //添加角色表单
      addRoleForm: {
        roleName: '',
        roleDesc: '',
      },
      //添加角色规则
      addRoleRules: {
        name: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
        desc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '长度在 3 到 10 个字符',
            trigger: 'blur',
          },
        ],
      },
      //编辑角色表单
      rolesRuleForm: {
        name: '',
        desc: '',
        id: '',
      },
    }
  },
  created() {
    this.getroleList()
  },
  methods: {
    //获取权限列表
    async getroleList() {
      const { data: res } = await this.$http.get('roles')

      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败！')
      }
      this.roleList = res.data
    },
    //根据id删除对应的权限
    async removeRightById(role, rightsId) {
      //弹框提示用户是否删除
      const confirmResult = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('取消了删除')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightsId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.warning('删除失败，发生了一些未知错误！')
      }

      role.children = res.data
      return this.$message.success('删除成功！')
    },
    //打开设置权限对话框
    async showSetRightDialog(role) {
      this.roleId = role.id
      //获取所有权限列表
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('获取列表失败！')
      }
      this.rightsList = res.data

      //递归获取三级节点的id
      this.getLeafKeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },
    setRightsClick(data) {
      console.log(data)
    },

    //通过递归的形式，获取角色下所有三级权限的id，并保存到defKeys数组中
    getLeafKeys(node, arr) {
      //如果当前node节点，不包含children属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }
      //访问每一个节点
      node.children.forEach((item) => this.getLeafKeys(item, arr))
    },
    setRightDialogClose() {
      this.defKeys = []
    },
    //根据角色id和权限修改权限
    async allotRights() {
      const keys = [
        ...this.$refs.setRightTree.getCheckedKeys(),
        ...this.$refs.setRightTree.getHalfCheckedKeys(),
      ]
      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        {
          rids: idStr,
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('更新失败！')
      }
      this.$message.success('更新成功！')
      this.getroleList()
      this.setRightDialogVisible = false
    },
    //添加角色视图
    addRoleDialog() {
      this.addRoleDialogVisible = true
    },
    //确定提交添加角色
    async subAddRole() {
      const { data: res } = await this.$http.post('roles', this.addRoleForm)
      if (res.meta.status !== 201) return this.$message.error('添加失败！')
      this.$message.success('添加成功！')
      this.getroleList()
      this.addRoleDialogVisible = false
    },
    addRoleColse() {
      this.$refs.addRoleForm.resetFields()
    },
    //根据id删除角色
    async deleteRole(role) {
      const deleteRole = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      if (deleteRole !== 'confirm') {
        return this.$message.info('您取消了删除！')
      }
      const { data: res } = await this.$http.delete('roles/' + role.id)
      if (res.meta.status !== 200) {
        return this.$message.warning('删除失败，发生了一些未知错误！')
      }
      this.getroleList()
      return this.$message.success('删除成功！')
    },
    //编辑角色
    editRole(role) {
      this.editRoleDialogVisible = true
      this.rolesRuleForm.name = role.roleName
      this.rolesRuleForm.desc = role.roleDesc
      this.rolesRuleForm.id = role.id
    },
    //编辑角色确定
    async editRoleOk() {
      const { data: res } = await this.$http.put(
        'roles/' + this.rolesRuleForm.id,
        {
          roleName: this.rolesRuleForm.name,
          roleDesc: this.rolesRuleForm.desc,
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('编辑失败！')
      }
      this.getroleList()
      this.editRoleDialogVisible = false
      return this.$message.success('编辑成功！')
    },
  },
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>